<template>
    <div class="searAddre">
        <Header>
            <i slot="first" class="iconfont icon-prev" @click="$router.go(-1)"></i>
            <p slot="mid">搜索地址</p>
            <div slot="last"></div>
        </Header>
        <div class="searAddre_one">
            <input type="text" placeholder="请输入小区/写字楼/学校等"  v-model="merchant"><button @click="add()">确认</button>
        </div>
        <div class="searAddre_p"><p>为了满足商家的送餐要求,建议您从列表中选取地址</p></div> 
        <div class="searAddre_two">
            <div class="searAddre_p3">
                <p>找不到地址？</p>
                <p>请尝试输入小区，写字楼或学校名</p>
                <p>详细地址（如门牌号）可稍后输入哦。</p>
            </div>
        </div>
        <div class="list">
            <ul>
                <li v-for="(v,i) in info" :key="i" @click="skip(v)">
                    <div>{{v.name}}</div>
                    <div>{{v.address}}</div>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import Header from "@/page/City/Header"
export default {
    components:{
        Header
    },
   data() {
      return {
        merchant:"",
        info:[]
      }
   },
   created() {
   },
   methods: {
       add(){
            this.axios.get(`http://elm.cangdu.org/v1/pois?type=nearby&keyword=${this.merchant}`).then((res)=>{
            // console.log(res.data)
            this.info=res.data
           })
       },
       //点击确认跳转到搜索地址页
       skip(v){
           this.$router.push(`/home/newaddress?name=${v.name}`)
       }
   }
}
</script>

<style lang="scss">
html,#app,body,.box{
    width: 100%;
    height: 100%;
}
.searAddre{
    width: 100%;
    height: 100%;
    position: relative;
    .searAddre_one{
        background: white;
        margin-top: 0.8rem;
        width: 100%;
        padding: 0.2rem 0.5rem;
        font-size: 0.3rem;
        >input{
            width: 80%;
            font-size: 0.25rem;
            padding: 0.1rem 0rem 0.1rem 0.2rem;
            outline: none;
            border-radius: 2%;
            background: #f2f2f2;
            border: none;
            font-weight: bold;
        }
        >button{
            width: 15%;
            background: #3399ff;
            color: white;
            border: none;
            border-radius: 5px;
            line-height: 0.55rem;
            margin-left: 0.3rem;
        }
        }
        .searAddre_p{
            width: 100%;
            background: #FFF6E4;
            p{
                height: 0.5rem;
                font-size: 0.29rem;
                color:#FF9858;
                margin: 0.1rem 0;
                text-align: center
            }
        }
        .list{
            width: 100%;
            background: #F2F2F2;
            z-index: 10;
            position: absolute;
            top:1.55rem;
            ul{
                list-style:none;
                width: 100%;
                li{
                    width: 100%;
                    border-bottom: 1px solid rgb(184, 183, 183);
                    line-height: 0.55rem;
                    font-size: 0.3rem;
                    div{
                        margin-left: 0.5rem;
                        color: #969696;
                    }
                }
            }
        }
    .searAddre_two{
        background: #F2F2F2;
        .searAddre_p3{
            width: 100%;
            height: 10%;
            text-align: center;
            position: absolute;
            margin: auto;
            top:0px;
            bottom: 0px;
            right: 0px;
            left: 0px;
            p{
                color: #A7A7A7;
                text-align: center;
                font-size: 0.3rem;
            }
        }
    }
    
}

</style>


